import { ORIGIN } from "./../../../common/network"
import axios from "axios"

const ROUTES = {
  SUGGESTIONS: query => `https://www.zillowstatic.com/autocomplete/v2/suggestions?q=${query}`,
}

const getSuggestions = async query => {
  try {
    const response = await axios.get(ROUTES.SUGGESTIONS(query))
    return response
  } catch (error) {
    console.error(error)
  }
}

export { getSuggestions, ROUTES }
