import React, { useState, useEffect, useCallback, useRef } from "react"
import pt from "prop-types"
import { Text, Box, TextInput } from "grommet"
import { useQueryParam, StringParam } from "use-query-params"
import { Search } from "grommet-icons"
import { getSuggestions } from "../utils/events"
import useDebounce from "../../../hooks/useDebounce"
import useNavigate from "../../../hooks/useNavigate"

const AddressSearch = ({ placeholder, width }) => {
  const [suggestionOpen, setSuggestionOpen] = useState(false)
  const [, updateState] = useState()
  const forceUpdate = useCallback(() => updateState({}), [])
  const [query, setQuery] = useState("")
  const [suggestions, setSuggestions] = useState([])
  const boxRef = useRef()
  const { debounce } = useDebounce()

  useEffect(() => {
    forceUpdate()
  }, [forceUpdate])

  const onChange = async e => {
    const query = e.target.value
    setQuery(query)
    debounce({
      func: async () => {
        if (!query.trim()) {
          setSuggestions([])
        } else {
          const s = await getSuggestions(query)
          setSuggestions(s)
        }
      },
      wait: 200,
    })()
  }

  const onSelect = event => {
    setQuery("")
    setSuggestions([])
    // navigate(`${ROUTES.DETAILS}/${event.suggestion.value.ticker}`)
  }

  const renderSuggestions = () => {
    return suggestions.map(({ display }, index, list) => ({
      label: (
        <Box direction="row" border={{ top: { size: "xsmall" }, color: "light-5" }} pad="xsmall">
          <Box pad={{ right: { size: "medium" } }} margin={{ left: "small", right: "small" }}>
            <Text size="medium">{display.split(",")[0]}</Text>
          </Box>
          <Box>
            <Text color="status-unknown" size={"medium"}>
              {display.split(",")[1]}
            </Text>
          </Box>
        </Box>
      ),
      value: { display },
    }))
  }

  return (
    <Box
      ref={boxRef}
      width={width || "xlarge"}
      direction="row"
      height="40px"
      margin={{ left: "medium" }}
      round="xsmall"
      align="center"
      background={"light-1"}
      pad={{ horizontal: "xsmall" }}
      elevation={suggestionOpen ? "medium" : undefined}
      border={{
        side: "all",
        color: suggestionOpen ? "transparent" : "border",
      }}
      style={suggestionOpen ? { borderBottomLeftRadius: "0px", borderBottomRightRadius: "0px" } : {}}
    >
      <TextInput
        type="search"
        plain
        value={query}
        onChange={onChange}
        dropTarget={boxRef.current}
        onSelect={onSelect}
        placeholder={placeholder}
        suggestions={renderSuggestions()}
        onSuggestionsOpen={() => setSuggestionOpen(true)}
        onSuggestionsClose={() => setSuggestionOpen(false)}
      />
      <Box>
        <Search color="brand" />
      </Box>
    </Box>
  )
}

export default AddressSearch
