import Cookies from "js-cookie"
import { saveAs } from "file-saver"
import * as queryString from "query-string"
import { fetch as fetchPolyfill } from "whatwg-fetch"
const IS_PROD = process.env.NODE_ENV === "production" && window.location.protocol == "https:"
const ORIGIN = `${window.location.protocol}//${IS_PROD ? window.location.host : window.location.hostname + ":8000"}`

const fetchConfig = {
  method: "GET",
  cache: "no-store", // none of these requests should be cached
  headers: {
    "Content-Type": "application/json",
    "X-CSRF-Token": Cookies.get("antiCsrfToken"),
    credentials: "include",
    cache: "no-store", // none of these requests should be cached
  },
}

const fetchPostConfig = body => ({
  ...fetchConfig,
  body: JSON.stringify(body),
  method: "POST",
})

const fetchPutConfig = body => ({
  ...fetchConfig,
  body: JSON.stringify(body),
  method: "PUT",
})

const fetchDeleteConfig = () => ({
  ...fetchConfig,
  method: "DELETE",
})

const fetchPostFormConfig = body => ({
  ...fetchConfig,
  method: "POST",
  headers: {
    ...fetchConfig.headers,
    "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
  },
  body: queryString.stringify(body),
})

const fetchPostFile = (tag, file, type) => {
  const data = new FormData()
  data.append("file", file)
  data.append("tag", tag)
  data.append("type", type)

  const body = {
    ...fetchConfig,
    method: "POST",
    headers: {
      ...fetchConfig.headers,
      "Content-Type": undefined,
    },
    body: data,
  }
  delete body.headers["Content-Type"]
  return body
}

const lensFetch = async (route, config) => {
  const monkeyRoute = route
  return await fetchPolyfill(monkeyRoute, config)
}

export {
  fetchConfig,
  fetchPostConfig,
  fetchPutConfig,
  fetchDeleteConfig,
  fetchPostFormConfig,
  fetchPostFile,
  lensFetch,
  IS_PROD,
  ORIGIN,
}
